const markdownContent = `

# 项目来源

## 引子

收到行权邮件及其 pdf 附件后，我看了邮件及附件好几遍，但是没有看太懂（暴露智商了，哈哈）。

到家后，让爱人帮忙参谋参谋，TA 说看懂了，但没有看明白细节，让我自己仔细看（锅又回来了）。

于是想，我该如何去理解邮件及附件信息呢？

由于缺乏期权及行权相关的背景知识，传统的阅读 + 同时大脑分析的方式，我理解信息的速率和准确率似乎有点低。

有让自己更快速且准确理解信息的其他方式吗？

前端开发是我擅长的，邮件及附件的核心信息似乎是一个公式计算器，如果采用日常工作中软件开发流程:

1. 产品需求分析
1. 产品拆解，模块化
1. 写代码
1. 测试
1. bug fix

似乎能帮助我理解邮件及附件信息。

## 第一个版本

说干就干，每天下班哄娃睡熟后，正好写一写。

按照日常工程的开发流程， 花了两个晚上写出了 node 版本 —— 一个 ts 文件。

我心里有点小愉悦:

- 软件开发流程，帮助我理解了邮件及附件大部分信息，达到了预期目的
- 软件开发流程，也许是一套思维方式，如果运用到日常生活中，可能会产生意想不到的力量

## 迭代

为了准确理解邮件及附件的信息，我花费了挺长时间，且其中夹杂着心烦——在未想到采用软件开发模式之前。

开发完 node 版本计算器，继而的想法是:

1. 其他同学是不是也要花一些时间来理解信息？是不是也有这样的心烦？
1. 可以写一个文档，写明如何让 node 版本跑起来的详细步骤
1. node 版本无界面，在命令行跑，展示结果比较丑陋，感觉不是很满意
1. 不如写一个简单的网站，有良好的交互和界面——前端开发恰恰也是我擅长的
   - 无论是开发还是非开发的同学，都能很方便地使用该网站
   - 可以直观地看到计算和结果，并不需要去理解邮件及附件，省时省力
   - 我对邮件及附件的理解可能存在 bug 的，如果其他同事使用该网站，能帮助我更准确理解邮件及附件的信息，并继续改善网站（即 测试 + bugfix 流程）

于是，将 node 版本改成了基于 Vue 的单页面应用——即这个网站 ( https://fudesign2008.github.io/calculator/ )。

## 思想收获

- 软件是思考力的结晶，是可以让任何人任何时间段重复使用的工具。
- 软件开发流程，也许是一套思维方式，如果运用到日常生活中，可能会产生意想不到的力量

以上是个人一点粗浅的心得，记之。


## Donate

如果本网站或者以上的粗浅心得对你有帮助，可以扫描下面二维码进行赞助。

原创不易，丰俭由君。

`

