import axios from 'axios'

let exchangeRate = -1

interface Rate {
  id: number
  rate: number
}

const ChinaId = 356

function getRate(rates: Rate[]): number {
  const found = rates.find(item => {
    return item.id === ChinaId
  })

  if (!found) {
    return -1
  }
  return found.rate
}

async function getExchangeRate(): Promise<number> {
  if (exchangeRate !== -1) {
    return exchangeRate
  }
  const url = 'https://www.mycurrency.net/US.json'
  let response: any = null
  try {
    response = await axios.get(url)
  } catch (ex) {
    // do nothing
  }
  if (response && response.data && response.data.rates) {
    const rate = getRate(response.data.rates as Rate[])
    if (rate !== -1) {
      exchangeRate = rate
    }
  }

  return exchangeRate
}

export { getExchangeRate }
